* {
  box-sizing: border-box; }

.container {
  width: 100%;
  margin: 0 auto; }

.container:after,
.row:after {
  content: "";
  display: table;
  clear: both; }

.row {
  margin-left: -15px;
  margin-right: -15px; }

[class*="col-"] {
  float: left;
  padding: 0 1em;
  width: 100%;
  min-height: 1px;
  display: inline-block; }

